<template>
    <div id="Confirm" class="layout">
      <p class="tit">确认提示框（$confirm）</p>
      <div class="base_box">
       <Button
          :butContent="'确认提示框'"
          @click="ConfirmFn()"
        />
      </div>
    </div>
</template>

<script>
    export default {
        props: {

        },
        components: {

        },
        data(){
            return {
                
            }
        },
        methods: {
             // 确认提示框调用方式
                ConfirmFn() {
                    this.$confirm({
                        type: "提示标题",
                        msg: "提示内容自定义，全局使用",
                        btn: {
                            ok: "确定",
                            no: "取消",
                        },
                    }) .then(() => {
                        // 自定义弹框
                        this.$tips({
                            type:'success',
                            msg:"确认",
                            top:30,
                            time:1000
                        })
                    }).catch(() => {
                        // 自定义弹框
                        this.$tips({
                            type:'info',
                            msg: " 取消",
                            top:30,
                            time:1000
                        })
                    });                
                },
        }
    }
</script>

