<template>
    <div id="Tips" class="layout">
      <p class="tit">信息提示框（$tips）</p>
       <Button
          :butContent="'信息提示'"
          :float="'l'"
          :type="'success'"
          :Width="'40%'"
          @click="tipsFn()"
        />
        <Button
          :butContent="'标题信息提示'"
          :float="'r'"
          :type="'success'"
          :Width="'40%'"
          @click="tipsFn1()"
        />
      </div>
</template>

<script>
    export default {
        props: {

        },
        components: {

        },
        data(){
            return {
                
            }
        },
        methods: {
            // 信息提示框按键展示
            tipsFn(){
                // 自定义弹框
                this.$tips({
                    type:'success',
                    msg: '提示信息文字',
                    top:30,
                    time:1000
                })
            },
            // 标题信息提示框按键展示
            tipsFn1(){
                // 自定义弹框
                this.$tips({
                    type:'success',
                    tit:'提示标题',
                    msg: '提示信息文字',
                    top:30,
                    time:1000
                })
            },
        }
    }
</script>

