<template>
    <div id="Pagination" class="layout">
      <p class="tit">分页器1（Pagination）</p>
      <div class="base_box">
        <Button
              :butContent="'重置分页器'"
              :Width="'150px'"
              :BGcolor="'#fff'"
              :lineHeight="30"
              :FontColor="'#00f'"
              @click="setcurrentPage"
        />
       <Pagination
            :Count="bigCount"
            :currentPage="currentPage"
            @getCurrentPage="getpage"
       />
      </div>
    </div>
</template>

<script>
    export default {
        props: {

        },
        components: {

        },
        data(){
            return {
                    // 分页器参数
                    currentPage: 1, //初始重置当前值
                    bigCount: 30,   //分页器1总页数
                    minCount: 20,   //分页器2总页数
            }
        },
        methods: {
            // 分页  1
            getpage(val) {
                this.currentPage = val;
                // 自定义弹框
                this.$tips({
                    type:'success',
                    msg: `点击了第${val}页`,
                    top:30,
                    time:100
                })
            },
            // 重置分页器当前页面值
            setcurrentPage() {
                this.currentPage = 1; //重置当前页
            },
        }
    }
</script>

